import uniIcon from '../uni-icon/uni-icon.vue'
export default {
  name: 'UniCollapseItem',
  components: {
    uniIcon
  },
  props: {
    title: { // 列表标题
      type: String,
      default: ''
    },
    name: { // 唯一标识符
      type: [Number, String],
      default: 0
    },
    disabled: { // 是否禁用
      type: Boolean,
      default: false
    },
    showAnimation: { // 是否显示动画
      type: Boolean,
      default: false
    },
    open: { // 是否展开
      type: Boolean,
      default: false
    },
    thumb: { // 缩略图
      type: String,
      default: ''
    }
  },
  data () {
    /**
     * TODO 兼容新旧编译器
     * 新编译器（自定义组件模式）下必须使用固定数值，否则部分平台下会获取不到节点。
     * 随机数值是在旧编译器下使用的，旧编译器模式已经不推荐使用，后续直接废掉随机数值的写法。
     */
    const elId = this.__callback_hook ? 'uni_collapse_item' : `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
    return {
      isOpen: false,
      height: 'auto',
      elId: elId
    }
  },
  watch: {
    open (val) {
      this.isOpen = val
    }
  },
  inject: ['collapse'],
  created () {
    this.isOpen = this.open
    this.nameSync = this.name ? this.name : this.collapse.childrens.length
    this.collapse.childrens.push(this)
    if (this.collapse.accordion) {
      if (this.isOpen) {
        let lastEl = this.collapse.childrens[this.collapse.childrens.length - 2]
        if (lastEl) {
          this.collapse.childrens[this.collapse.childrens.length - 2].isOpen = false
        }
      }
    }
  },






  onReady () {
    this.getSize()
  },

  methods: {
    getSize () {
      if (this.showAnimation) {
        uni.createSelectorQuery().in(this).select(`#${this.elId}`).boundingClientRect().exec((ret) => {
          this.height = ret[0].height + 'px'
        })
      }
    },
    onClick () {
      if (this.disabled) {
        return
      }
      if (this.collapse.accordion) {
        this.collapse.childrens.forEach(vm => {
          if (vm === this) {
            return
          }
          vm.isOpen = false
        })
      }
      this.isOpen = !this.isOpen
      this.collapse.onChange && this.collapse.onChange()
    }
  }
}
